import './JS2Native.css'
import React, { useEffect, useState } from 'react'
import Modal from 'react-modal'

function DynamicJs2Native() {

  const getCookie = name => {
    const nameEQ = encodeURIComponent(name) + '='
    const cookies = document.cookie.split(';')

    for (let i = 0; i < cookies.length; i++) {
      let cookie = cookies[i].trim()
      if (cookie.indexOf(nameEQ) === 0) {
        return decodeURIComponent(cookie.substring(nameEQ.length))
      }
    }
    return null
  }

  const setCookie = (name, value) => {
    document.cookie = `${encodeURIComponent(name)}=${encodeURIComponent(value)}`
  }

  const [bridgeName, setBridgeName] = useState(getCookie('bridgeName'))
  const [functionName, setFunctionName] = useState(getCookie('functionName') ?? 'postMessage')
  const [alertMsg, setAlertMsg] = useState('')
  const [isCopied, setIsCopied] = useState(false)
  let isProcessing = false

  const isIos = () => {
    const ua = navigator.userAgent
    return !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
  }

  // 
  const [dataObjects, setDataObjects] = useState([{
    id: Date.now(),
    action: 'fetchAppInfo',
    params: [{
      key: '',
      value: ''
    }],
    result: null // 
  }])

// 
  const addDataObject = () => {
    setDataObjects([{
      id: Date.now(),
      action: '',
      params: [{
        key: '',
        value: ''
      }],
      result: null
    }, ...dataObjects])
  }

// 
  const removeDataObject = (index) => {
    const newDataObjects = [...dataObjects]
    newDataObjects.splice(index, 1)
    setDataObjects(newDataObjects)
  }

// action
  const updateAction = (index, value) => {
    const newDataObjects = [...dataObjects]
    newDataObjects[index].action = value
    setDataObjects(newDataObjects)
  }

// 
  const addParam = (dataObjIndex) => {
    const newDataObjects = [...dataObjects]
    newDataObjects[dataObjIndex].params.push({
      key: '',
      value: ''
    })
    setDataObjects(newDataObjects)
  }

// 
  const removeParam = (dataObjIndex, paramIndex) => {
    const newDataObjects = [...dataObjects]
    newDataObjects[dataObjIndex].params.splice(paramIndex, 1)
    setDataObjects(newDataObjects)
  }

// 
  const updateParam = (dataObjIndex, paramIndex, field, value) => {
    const newDataObjects = [...dataObjects]
    newDataObjects[dataObjIndex].params[paramIndex][field] = value
    setDataObjects(newDataObjects)
  }

// 
  const assembleSingleData = (dataObjIndex) => {
    const dataObj = dataObjects[dataObjIndex]

    // 
    const validParams = dataObj.params.filter(param => param.key.trim() !== '')

    // 
    const paramsObj = {}
    validParams.forEach(param => {
      paramsObj[param.key] = param.value
    })

    // 
    const data = {
      action: dataObj.action,
      identifer: dataObj.action,
      params: paramsObj
    }

    const displayData = {
      bridgeName: bridgeName,
      functionName: functionName,
      data: data
    }

    // 
    const newDataObjects = [...dataObjects]
    newDataObjects[dataObjIndex].result = displayData
    setDataObjects(newDataObjects)

    // 
    if (bridgeName && functionName && dataObj.action) {
      window[bridgeName]?.[functionName]?.(JSON.stringify(data))
      if (window[bridgeName] != null) {
        setCookie('bridgeName', bridgeName)
        if (window[bridgeName]?.[functionName] != null) {
          setCookie('functionName', functionName)
        } else {
          setAlertMsg('Error:\nCheck your native function name')
        }
      } else {
        setAlertMsg('Error:\nCheck your native bridge name')
      }
    }
  }

  function onFileChooseClick() {
    document.getElementById('fileInput')
      .click()
  }

  function formatFileSize(bytes) {
    if (bytes === 0) return '0 Bytes'
    const k = 1024
    const sizes = ['Bytes', 'KB', 'MB', 'GB']
    const i = Math.floor(Math.log(bytes) / Math.log(k))
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
  }

  const listenFileInput = () => {
    const fileInput = document.getElementById('fileInput')
    const fileInfo = document.getElementById('file-info')
    fileInput.addEventListener('change', function (e) {
      if (isProcessing) {
        return
      }
      isProcessing = true

      const files = e.target.files
      let info = ''

      if (files.length > 0) {
        info += `<p>Selected ${files.length} file(s):</p><ul>`

        for (let i = 0; i < files.length; i++) {
          info += `<li>${files[i].name} (${formatFileSize(files[i].size)})</li>`
        }

        info += '</ul>'
      } else {
        isProcessing = false
        info = '<p>No files selected</p>'
      }

      fileInfo.innerHTML = info

      const imageFiles = Array.from(files)
        .filter(file => file.type.startsWith('image/'))
      if (imageFiles.length > 0) {
        const previewContainer = document.getElementById('previewContainer')
        previewContainer.innerHTML = ''

        imageFiles.forEach((file, index) => {
          const reader = new FileReader()
          reader.onload = function (e) {
            const img = new Image()
            img.src = e.target.result

            img.onload = function () {
              // 
              const previewCard = document.createElement('div')
              previewCard.className = 'preview-card'

              // 
              const previewImage = document.createElement('img')
              previewImage.className = 'preview-image'
              previewImage.src = e.target.result
              previewImage.alt = file.name

              // 
              const previewInfo = document.createElement('div')
              previewInfo.className = 'preview-info'

              const previewName = document.createElement('div')
              previewName.className = 'preview-name'
              previewName.textContent = file.name

              const previewDetails = document.createElement('div')
              previewDetails.className = 'preview-details'

              const fileSize = document.createElement('div')
              fileSize.textContent = `: ${formatFileSize(file.size)}`

              const imageSize = document.createElement('div')
              imageSize.textContent = `: ${img.width}  ${img.height} `

              previewDetails.appendChild(fileSize)
              previewDetails.appendChild(imageSize)
              previewDetails.appendChild(previewName)

              previewInfo.appendChild(previewDetails)

              previewCard.appendChild(previewImage)
              previewCard.appendChild(previewInfo)
              const key = `${file.name}_${file.key}`

              previewCard.id = key

              let hasAppend = false
              for (let i = 0; i < previewContainer.children.length; i++) {
                const id = previewContainer.children[i].id
                if (id === key) {
                  hasAppend = true
                  return
                }
              }

              if (!hasAppend) {
                previewContainer.appendChild(previewCard)
              }
            }
          }

          reader.readAsDataURL(file)
          isProcessing = false
        })
      } else {
        isProcessing = false
      }
    })
  }

  useEffect(() => {
    listenFileInput()
  }, [listenFileInput])

  window.callWeb = function (result) {
    const {
      identifer,
      status,
      data,
      errorMessage
    } = result
    setAlertMsg(JSON.stringify(result, null, 2))
  }

  const copyToClipboard = async (text) => {
    try {
      await navigator.clipboard.writeText(text)
      setIsCopied(true)
      setTimeout(() => setIsCopied(false), 2000)
    } catch (err) {
      // 
      const textArea = document.createElement('textarea')
      textArea.value = text
      document.body.appendChild(textArea)
      textArea.select()
      document.execCommand('copy')
      document.body.removeChild(textArea)
      setIsCopied(true)
      setTimeout(() => setIsCopied(false), 2000)
    }
  }

  return (
    <div className="App-Content">

      <p style={{ marginTop: 24 }}/>

      <p style={{ textAlign: 'center' }}>
        <input type="file" accept="video/*,image/*" id="fileInput" multiple style={{ display: 'none' }}/>
        {/* <input type="file" accept="image/*" id="fileInput" multiple style={{ display: 'none' }}/> */}
        {/* <input type="file" accept="video/*" id="fileInput" multiple style={{ display: 'none' }}/> */}
        <input type="file" id="fileInput" multiple style={{ display: 'none' }}/>
        <button type="button" style={{ fontSize: 30 }} onClick={onFileChooseClick}>
          Select Files
        </button>

        <div id="file-info"></div>
        <div className="preview-container" id="previewContainer"/>
      </p>

      <div className={'divider'}/>
      <div className={'params-section'}/>

      <div className="param-item params-section">
        <input
          className="param-input"
          type="text"
          id="bridgeName"
          value={bridgeName}
          onChange={(e) => setBridgeName(e.target.value)}
          placeholder="Bridge"
        />
        <input
          className="param-input"
          type="text"
          id="functionName"
          value={functionName}
          onChange={(e) => setFunctionName(e.target.value)}
          placeholder=""
        />
      </div>

      <div className={'params-section'}/>
      <button
        type="button"
        style={{
          fontSize: 20,
          marginLeft: '2px',
          marginRight: '2px'
        }}
        onClick={addDataObject}
      >
        + Action
      </button>

      <div className={'params-section'}/>
      <div className={'divider-dash'}/>

      <div>
        {dataObjects.map((dataObj, dataObjIndex) => (
          <div key={dataObj.id} className="action-card">
            <div style={{
              display: 'flex',
              justifyContent: 'flex-end'
            }}>
              <button
                className="remove-param-btn"
                onClick={() => removeDataObject(dataObjIndex)}
              >
                 Action
              </button>
            </div>

            <div className="form-group-center">
              <label htmlFor={`action-${dataObjIndex}`}>Action</label>
              <input
                type="text"
                id={`action-${dataObjIndex}`}
                value={dataObj.action}
                onChange={(e) => updateAction(dataObjIndex, e.target.value)}
                placeholder="action"
              />
            </div>

            <div>
              <div className="params-header">
                <label>Params ()</label>
                <button className="add-param-btn" onClick={() => addParam(dataObjIndex)}>
                  + 
                </button>
              </div>

              {dataObj.params.map((param, paramIndex) => (
                <div key={paramIndex} className="param-item">
                  <input
                    type="text"
                    className="param-input"
                    value={param.key}
                    onChange={(e) => updateParam(dataObjIndex, paramIndex, 'key', e.target.value)}
                    placeholder=""
                  />
                  <input
                    type="text"
                    className="param-input"
                    value={param.value}
                    onChange={(e) => updateParam(dataObjIndex, paramIndex, 'value', e.target.value)}
                    placeholder=""
                  />
                  <button
                    className="remove-param-btn"
                    onClick={() => removeParam(dataObjIndex, paramIndex)}
                  >
                    
                  </button>
                </div>
              ))}
            </div>

            {/*  */}
            <button
              className="submit-btn"
              onClick={() => assembleSingleData(dataObjIndex)}
            >
              
            </button>

            {/*  */}
            {dataObj.result && (
              <div className="result-section">
                <div className="result-title">Action: {dataObj.action}:</div>
                <pre>{JSON.stringify(dataObj.result, null, 2)}</pre>
              </div>
            )}

            {dataObjIndex < dataObjects.length - 1 && <div className="divider-dash"/>}
          </div>
        ))}
      </div>

      <Modal
        style={{
          content: {
            // wordWrap: 'break-word',  // 
            // whiteSpace: 'pre-wrap',  // 
            // overflowWrap: 'break-word', // 
            maxHeight: '80vh',
            // margin: 'auto',
            borderRadius: '8px',
            overflow: 'auto',
            // justifyContent: 'center',
            // alignItems: 'center',
            left: '20px',
            right: '20px',
            inset: 'auto',
            maxWidth: '99vw' // 
          },
          overlay: {
            display: 'flex',
            backgroundColor: 'rgba(0, 0, 0, 0.5)', // 
            alignItems: 'center',
            justifyContent: 'center'
          }
        }}
        isOpen={alertMsg !== ''}
        onRequestClose={() => {
          setAlertMsg('')
          setIsCopied(false)
        }}
      >
        <div>
          <div style={{
            display: 'flex',
            justifyContent: 'flex-end'
          }}>
            <button
              className="copy"
              onClick={() => copyToClipboard(alertMsg)}
            >
              {isCopied ? ' ' : ''}
            </button>
          </div>
          <pre
            style={{
              // whiteSpace: 'pre-wrap'
              // wordWrap: 'break-word',
              margin: 0
            }}
          >
            {alertMsg}
          </pre>
        </div>
      </Modal>
    </div>
  )
}

export default DynamicJs2Native

